.general-form__edit {
 
  background-color: #3A6055
  ;
  form {  
  display: grid;
  grid-template-columns: 200px 1fr;
  grid-gap: 16px;
  
  background: #f9f9f9;
  border: 1px solid #c1c1c1;
  margin: 0em auto;
  max-width: 800px;
  padding: 1em;
  color: #CD4F1A;
  font-weight: bold;

  label {
    grid-column: 1 / 2;
  }
 
  input,
  button {
    grid-column: 2 / 3;
    
  }
  input{
    border-radius: 5px;
    height: 25px;
  }
  button {
    background-color:#3A6055
    ;
    height: 30px;
    width: 200px;
    margin: 0 auto;
    color: white;
    ;
    font-size: 1.3em;
  }
  button:hover{
    opacity: 0.8;
    color:#eb5413;
  }
  
}

  &__body{
    background-color: yellow;
  }
}
.general-form__view{
  margin-right: 125px;
  text-align: right;
  padding-bottom: 3.5em;

  span{
  color: #f06f8f;  
  }
  &__name
  {
    font-weight: bold;
    font-size: 24px
  }
  h4{
    margin-top:-1.1em;
    font-style: italic;
    color: white;
    text-shadow: 2px 2px #010101;;
  }
  img{
    width: 200px;
    height: auto;
  }
 
  p{
    color: white; 
    
  }
}